.vi-toggler {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: url(../vi/eye.svg) center no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
  margin-left: 12px;
  cursor: pointer;
  position: relative;
}

html.vi .vi-toggler:before {
  content: "";
  position: absolute;
  left: -2px;
  right: -2px;
  top: 50%;
  margin-top: -1px;
  height: 2px;
  background: #FFF;
  -ms-transform: rotate(-35deg);
  transform: rotate(-35deg);
}

.f-contacts-items > * {
  margin-bottom: 25px;
}

.f-top {
  padding-bottom: 35px;
}

.search-toogler {
