}

.vi-start-btn {
  display: inline-block;
  vertical-align: middle;
  /*display: none;*/
  height: 50px;
  line-height: 46px;
  font-size: 18px;
  border: 0;
  padding: 0px 15px 0px 40px;
  font-weight: 700;
  text-align: center;
  border: 2px solid #000;
  cursor: pointer;
  color: #FFF !important;
  position: relative;
}

.vi-start-btn:before {
  content: "";
  position: absolute;
  left: 17px;
  top: 50%;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  background: url(../images/eye.svg) center no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
}

.vi-panel {
  display: none;
  visibility: hidden;
  opacity: 0;
  background: #FFF;
  padding: 15px 0 0;
  font-size: 1rem;
  position: relative;
  z-index: 9;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: auto;
  border-top: 1px solid #9EA5B4;
  z-index: 99;
}

.vi-panel.opened {
  display: block;
  visibility: visible;
  opacity: 1;
}

.vi-panel .flex {
  margin-left: -25px;
  margin-right: -25px;
  -ms-align-items: flex-end;
  align-items: flex-end;
}

.vi-panel-group {
  padding: 0px 25px;
  margin-bottom: 15px;
  -webkit-flex: 1 0 auto;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  max-width: 30%;
}
.vi-panel-group:not(:last-of-type) {
  border-right: 1px solid #a7a7a7;
}
@media (max-width: 1640px) {
  .vi-panel-group {
    max-width: none;
  }
}

