.burger-menu {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #FFFFFF;
  margin-top: 200px;
  z-index: 9;
  transform: translateX(-100%);
  transition: transform 0.3s;
  overflow-y: auto;
}
.burger-menu-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.burger-menu .container {
  width: 100%;
}
.burger-menu.active {
  transform: translateX(0%);
}
.burger-menu-nav {
  background-color: #F5F9FF;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.burger-menu-nav .container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  flex-grow: 1;
}
.burger-menu .row {
  height: 100%;
  flex-grow: 1;
}
.burger-menu-icon {
  width: 24px;
  height: 24px;
  transform: rotate(-90deg);
  fill: #1F2839;
  display: inline-block;
  vertical-align: middle;
  transition: fill 0.3s;
}
.burger-menu-left {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  border-right: 1px solid #E3E8F2;
  height: 100%;
  padding-top: 70px;
}
@media (max-width: 1366px) {
  .burger-menu-left {
    font-size: 16px;
    font-size: 1rem;
  }
}
.burger-menu-left li {
  margin-bottom: 38px;
  width: max-content;
}
@media (max-width: 1366px) {
  .burger-menu-left li {
    margin-bottom: 28px;
  }
}
.burger-menu-left li.active a {
